import axios from 'axios';

// 创建一个 axios 实例
const apiClient = axios.create({
  baseURL: 'http://localhost:8089/api',
  headers: {
    'Content-Type': 'application/json'
  }
});

// 统一的匹配接口
export const findMatch = async () => {
  const response = await apiClient.get('/match');
  return response.data;
};